.nav_toggle {
    display: block;
    height: var(--header-height);
    cursor: pointer;
    background-image: url('../assets/nivo-nav-bars.png');
    background-size: 26px 26px;
    background-repeat: no-repeat;
    background-position: center center;
}

header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: var(--header-background-color);
    background-image: -webkit-gradient(linear, right top, left top, from(#dc5a32), to(#c44a67));
    background-image: -webkit-linear-gradient(right, #dc5a32, #c44a67);
    background-image: -o-linear-gradient(right, #dc5a32, #c44a67);
    background-image: linear-gradient(-90deg, #dc5a32, #c44a67);
    height: var(--header-height);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

header nav {
    position: absolute;
    top: 0;
    right: 80px;
    height: var(--header-height);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
}

header a {
    color: var(--main-background-color);
    text-decoration: none;
    margin-left: 20px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
}

header a.active {
    background: var(--main-background-color);
}



